
.demo-block{border:solid 1px #eaeefb;border-radius:4px;transition:.2s
}
.demo-block.hover{box-shadow:0 0 8px 0 rgba(232,237,250,0.6),0 2px 4px 0 rgba(232,237,250,0.5)
}
.demo-block code{font-family:Menlo, Monaco, Consolas, Courier, monospace
}
.demo-block .demo-button{float:right
}
.demo-block .source{padding:24px
}
.demo-block .meta{background-color:#f9fafc;border-top:solid 1px #eaeefb;overflow:hidden;height:0;transition:height .2s
}
.demo-block .description{padding:18px 24px;width:40%;box-sizing:border-box;border-left:solid 1px #eaeefb;float:right;font-size:14px;line-height:1.8;color:#5e6d82;word-break:break-word
}
.demo-block .description p{margin:0 0 12px
}
.demo-block .description code{color:#5e6d82;background-color:#e6effb;margin:0 4px;transform:translateY(-2px);display:inline-block;padding:1px 5px;font-size:12px;border-radius:3px
}
.demo-block .highlight{width:60%;border-right:solid 1px #eaeefb
}
.demo-block .highlight pre{margin:0
}
.demo-block .highlight code.hljs{margin:0;border:none;max-height:none;border-radius:0
}
.demo-block .highlight code.hljs::before{content:none
}
.demo-block .demo-block-control{border-top:solid 1px #eaeefb;height:36px;box-sizing:border-box;background-color:#fff;border-bottom-left-radius:4px;border-bottom-right-radius:4px;text-align:center;margin-top:-1px;color:#d3dce6;cursor:pointer;transition:.2s;position:relative
}
.demo-block .demo-block-control i{font-size:12px;line-height:36px;color:#787878;transition:.3s;display:inline-block
}
.demo-block .demo-block-control i.hovering{transform:translateX(-40px)
}
.demo-block .demo-block-control span{position:absolute;transform:translateX(-30px);font-size:14px;line-height:36px;transition:.3s;display:inline-block
}
.demo-block .demo-block-control:hover{color:#20a0ff;background-color:#f9fafc
}
.demo-block .demo-block-control .text-slide-enter,.demo-block .demo-block-control .text-slide-leave-active{opacity:0;transform:translateX(10px)
}



